import React from 'react';
import {
    Switch,
    Route,
    useRouteMatch,
    Redirect,
  } from "react-router-dom";
  
import { CustomDrawer } from '../components';
import { Home, Servico, Cartao, Perfil, Agenda, Conta } from '../screens';

import { APPURL } from '../environment';

const Dashboard = () => {

    let match = useRouteMatch();

    return (
        <CustomDrawer >
            <Switch>
                <Route path={`${match.path}/co`}>
                    <Conta />
                </Route>
                <Route path={`${match.path}/ct`}>
                    <Cartao />
                </Route>
                <Route path={`${match.path}/s`}>
                    <Agenda />
                </Route>
                <Route path={`${match.path}/p`}>
                    <Perfil />
                </Route>
                <Route path={`${match.path}/s`}>
                    <Servico />
                </Route>
                <Route path={match.path}>
                    <Home />
                </Route>
            </Switch>
        </CustomDrawer>
    );
}

export default Dashboard;
