import React from 'react';
import { TwitterPicker } from 'react-color';

import {
  Container,
  WrapperInput,
  WrapperCard,
  Card,
  Logo,
  WrapperHeader,
  WrapperAdd,
} from './styles';

import { 
  Button,
} from '@material-ui/core';

import { useGenericModel } from '../../hooks';
import { APIURL } from '../../environment';
import { atualiza } from '../../utils/Services';

//https://casesandberg.github.io/react-color/
const Cartao = () => {

  const [cartao, setCartao, cartaoInicial] = useGenericModel('account_card');
  const [input, setInput] = React.useState({})
  const [file, setFile] = React.useState(null);

  React.useEffect(() => {
      setInput(cartao)
  },[cartao]);

  const handleChangeColor = (color) => {
      let novoInput = {...input}
      novoInput['cor'] = color.hex
      setInput(novoInput)
  }

  const handleChangeFile = (event) => {
      setFile(URL.createObjectURL(event.target.files[0]))
      let novoInput = {...input}
      novoInput['logo'] = event.target.files[0]
      setInput(novoInput)     
  }

  async function enviaFormularioProfile(){
    let novoInput = {...input}
    if(file){
      setFile(null)
    }else{
      delete novoInput.logo;
    }
    let result = await atualiza('account_card', novoInput)
    setCartao(result)
  }


  return (
    <Container >

        <WrapperHeader >
              <WrapperAdd >
                  <Button variant="contained" color="primary" onClick={enviaFormularioProfile} >
                      Atualizar
                  </Button>
              </WrapperAdd>
          </WrapperHeader>    

        <WrapperInput>
          <TwitterPicker
            triangle="hide"
            color={ input?.cor ? input?.cor : '#8ED1FC' }
            onChangeComplete={handleChangeColor}
          />
    
          <input type="file" onChange={handleChangeFile}/>
        </WrapperInput>

        <WrapperCard >
          <Card color={ input?.cor ? input?.cor : '#8ED1FC' } >
            <Logo src={ file ? file : input?.logo ? `${APIURL}${input?.logo}` : '' }/>
          </Card>
        </WrapperCard>
    </Container>
  );

}

export { Cartao }