import * as React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

// import DefaultLayoutNavigation from './DefaultLayoutNavigation'; 
// import PrivateRoute from './PrivateRoute';
// import Login from '../screens/Login';
// import Display from '../screens/Look/Display';

import { LoginPage, Cadastro } from '../screens';
import Dashboard from './dashboard';
import PrivateRoute from './private';
 
import { APPURL } from '../environment';

function NavegacaoInicial() {
  return (
    <Router>
        <Switch>
            <Route path={`${APPURL}/sign-in`}>
              <Cadastro />
            </Route>
            <Route path={`${APPURL}/login`}>
              <LoginPage />
            </Route>
            <PrivateRoute path={`${APPURL}/`}>
              <Dashboard />
            </PrivateRoute>
        </Switch>
    </Router>
  );
}

export { NavegacaoInicial } ;